<template>
  <div class="lrwa1Description">
    <div class="textColumn">
      <div class="myH3">{{ $t("lrwa1") }}</div>
      <div class="myH5">{{ $t("lrwa1C1") }}</div>
      <div class="setTable" v-for="item in lrwa1Table" :key="item.id">
        <div class="setImgBlock">
          <img class="setImg" :src="item.img" />
        </div>
      </div>
      <div class="videoBlock" v-for="item in videoJson[2].video" :key="item">
        <div
          class="ratio ratio-16x9 videoStyle"
          v-if="item.id === 'L_dYVqWzFqI'"
        >
          <iframe
            :src="item.video"
            class="embed-responsive-item"
            loading="lazy"
            allow="autoplay;"
            allowfullscreen
          ></iframe>
          <div class="overlay" @click="previewImgClick(item, $event)">
            <CIcon :icon="cilMediaPlay" class="setIcon" />
            <div
              class="thumbnail"
              :style="`
            background-image: url('${item.thumbnail}');`"
            ></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.videoStyle {
  margin: 60px auto auto auto;
}
.overlay {
  z-index: 120;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
}

/*去除低解析度縮圖上下黑邊*/
.thumbnail {
  position: absolute;
  z-index: 50;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  filter: brightness(0.4);
  pointer-events: none;
}

.setIcon {
  border-radius: 99%;
  border: #fff thick solid;
  width: 17%;
  padding: 1%;
  padding-left: 1.4%;
  pointer-events: none;
  z-index: 120;
  color: #fff;
}
.lrwa1Description {
}
.setImgBlock {
  margin: auto;
}
.setImg {
  max-width: 100%;
  margin-top: 30px;
}
.tableContainer {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
