</style>

<script>
import { reactive } from "vue";
import json from "@/assets/press/videoList.json";
import { CIcon } from "@coreui/icons-vue";
import { cilMediaPlay } from "@coreui/icons";
import axios from "axios";
export default {
  setup() {
    const videoJson = reactive(json);
    const lrwa1Table = [
      {
        img: require("@/assets/products/automation/lrwa1Img1.webp"),
      },
    ];
    const loadThumbnail = () => {
      let videos = videoJson[2].video;
      let url = `https://www.googleapis.com/youtube/v3/videos?id=${videos[1].id}&key=AIzaSyBXCxbE2nHTvZ7oQgVfiVT48HnDLR19sKI&part=snippet`;
      console.log("request url: " + url);
      axios(url)
        .then(function (response) {
          // handle success
          console.log(response);
          if (response.data.items[0].snippet.thumbnails.maxres != null)
            videos[1].thumbnail =
              response.data.items[0].snippet.thumbnails.maxres.url;
          else
            videos[1].thumbnail =
              response.data.items[0].snippet.thumbnails.high.url;
        })
        .catch(function (error) {
          // handle error
          console.log(
            `youtube api request failed! id = ${videos[1].id}`,
            error
          );
        });
    };

    loadThumbnail();
    const previewImgClick = (item, e) => {
      var clickedElement = e.target;
      $(clickedElement).hide();
      // show video
      console.log("clickedElement", clickedElement);
      item.video = `https://www.youtube.com/embed/${item.id}?autoplay=1`;
    };
    return {
      lrwa1Table,
      videoJson,
      previewImgClick,
      CIcon,
      cilMediaPlay,
    };
  },
};
</script>
